.App
{
    text-align:       center;
    background-color: #1e2228;
}

.App-logo
{
    height:         40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference)
{
    .App-logo
    {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header-name
{
    display:        flex;
    flex-direction: column;
    align-items:    self-start;
    font-size:      calc(10px + 2vmin);
    color:          white;
    padding:        15px;
    position:       absolute;
    top:            0;
    left:           0;
}

.App-main-text p.hello-text-block{
    font-size:      calc(15px + 2vmin);
    margin:    30px 0px;
}

.App-main-text
{
    min-height:      100vh;
    display:         flex;
    flex-direction:  column;
    align-items:     center;
    justify-content: center;
    font-size:       calc(5px + 2vmin);
    color:           white;
    position:        relative;
}


.App-main-text p
{
    max-width: 100vh;
    margin:    10px 0px;
}

.App-link
{
    color: #61dafb;
}

.contacts
{
    position:   absolute;
    bottom:     0;
    margin-top: 150px;
}

.contacts a
{
    color: #1c88e3;
}

.contact-social
{
    width: 30px;
}

@keyframes App-logo-spin
{
    from
    {
        transform: rotate(0deg);
    }
    to
    {
        transform: rotate(360deg);
    }
}


/* On screens that are 600px or less, set the background color to olive */
@media screen and (max-width: 600px)
{
    .App-main-text
    {
        font-size: calc(10px + 2vmin);
    }

    .App-main-text p
    {
        max-width: 50vh;
    }

    .contacts
    {
        position: initial;
        margin-top: 50px;
    }
}

@media screen and (max-width: 450px) {
    .App-main-text p.text-block:nth-child(2) {
        margin-top: 100px;
    }
}

